@use "../abstract/" as abs;

///////////
/// MAIN HOME PAGE
.mainhome {
	position: relative;

	&__header {
		background-color: var(--color-secondary-900);

		&__title {
			height: 100vh;
			display: flex;
			justify-content: center;
			align-items: center;
			color: var(--color-white);
			text-align: center;
			text-transform: uppercase;
			text-shadow: -3px 3px 3px var(--color-secondary-500);
			font-size: 7.41rem;
			font-family: var(--font-primary);
			font-weight: 600;
			letter-spacing: -8;
			word-spacing: 1rem;
		}
	}

	&__main {
		.mainhome__section {
			&__header {
				width: 100%;
				margin-bottom: 5rem;
				color: var(--color-secondary-300);
				text-shadow: 2px 2px 1px var(--color-secondary-100);
				font-family: var(--font-primary);
				font-weight: 600;
				font-size: clamp(6rem, 10vw, 7rem);
				letter-spacing: -3;
				text-transform: uppercase;
				text-align: center;
				border-bottom: 5px double var(--color-secondary-500);
			}
		}
	}
}

//////////////
/// CONTENT PAGE [3RD PAGE]
.content__page--container {
	.nav--container {
		grid-template-rows: 10vh 6vh 75vh;
		display: grid;
		gap: 1rem;

		h1 {
			grid-column: 1 / -1;
			grid-row: 1 / 2;
			font-family: var(--font-primary);
			font-weight: 600;
			font-size: 5rem;
			text-transform: uppercase;
			letter-spacing: -2;
			word-spacing: 0.7rem;
			@extend %center-me-full;

			animation-name: rainbowZoomIn;
			animation-duration: 4s;
			animation-delay: 0.2s;
			animation-iteration-count: infinite;
			animation-direction: alternate-reverse;

			@include abs.mq(xxl) {
				font-size: 4.5rem;
			}
			@include abs.mq(xl) {
				font-size: 4rem;
			}
			@include abs.mq(lg) {
				font-size: 3.6rem;
			}
			@include abs.mq(md) {
				font-size: 3.2rem;
			}
			@include abs.mq(sm) {
				font-size: 2.8rem;
			}
			@include abs.mq(xs) {
				font-size: 2.4rem;
			}
			@include abs.mq(xxs) {
				font-size: 2rem;
			}
		}

		svg {
			grid-row: 2 / 3;
			justify-self: self-end;
			align-self: center;

			height: 4rem;
			width: 4rem;
			margin-right: 5rem;
			fill: var(--color-accent-900);

			animation-name: rainbowZoomIn;
			animation-duration: 4s;
			animation-delay: 0.2s;
			animation-iteration-count: infinite;
			animation-direction: alternate-reverse;

			@include abs.mq(md) {
				height: 2.5rem;
				width: 2.5rem;
				margin-right: 2rem;
			}
		}

		.section {
			display: flex;
			align-items: flex-end;
			font-size: 5rem;
			font-family: var(--font-secondary);
			font-weight: 700;
			text-transform: uppercase;
			color: var(--color-accent-900);
			align-self: center;
			justify-self: start;
			margin-left: 10rem;
			margin-top: 3rem;
			letter-spacing: -2;
			font-style: italic;

			animation-name: slideToLeft;
			animation-duration: 2s;
			animation-delay: 0.2s;

			@include abs.mediaQuery(tab-land) {
				margin-left: 4rem;
				margin-bottom: 5rem;
			}

			@include abs.mediaQuery(tab-port) {
				margin-left: 1rem;
				font-size: 4.2rem;
			}

			@include abs.mediaQuery(mobile) {
				font-size: 4rem;
				margin-bottom: 0;
			}
		}

		.nav--indexes {
			grid-column: 1 / -1;
			display: flex;
			flex-wrap: wrap;

			@include abs.border(1, --color-accent-900);
			margin: 3rem;

			animation-name: slideToRight;
			animation-duration: 2s;
			animation-delay: 0.2s;

			@include abs.mediaQuery(mobile) {
				justify-content: space-evenly;
			}

			a:link {
				display: flex;
				align-items: center;
				justify-content: center;
				text-decoration: none;
				flex: 0 0 50%;
				font-size: 1.9rem;
				color: var(--color-accent-900);
				border: 1px solid black;
				text-align: center;
				transition: all 0.5s ease;

				&:active {
					transform: scale(0.9);
					color: var(--color-secondary-300);
					border: none;
				}

				&:last-of-type {
					border: none;
				}
				&:nth-of-type(even) {
					border-right: none;
					border-top: none;
				}
				&:nth-of-type(odd) {
					border-top: none;
					border-left: none;
				}
			}
			a:visited {
				color: var(--color-visited);
			}
		}
	}

	main section {
		h2 {
			position: relative;
			text-transform: uppercase;
			padding: 2.5rem 0;
			margin: 3rem 0;
			color: var(--color-accent-900);
			background-color: var(--color-secondary-200);
			transition: transform 0.7s ease-in-out;
			font-size: clamp(1.2rem, 4vw, 4rem);
			font-family: var(--font-secondary);
			font-weight: 700;
			letter-spacing: -3;
			word-spacing: 1rem;
			@extend %center-me-full;

			@include abs.mq(lg) {
				letter-spacing: -2;
			}
			@include abs.mq(md) {
				letter-spacing: -1;
			}
			@include abs.mq(sm) {
				letter-spacing: 0;
			}
		}
	}
	.content__area--container h3 {
		font-size: clamp(2rem, 6vw, 3.5rem);
		font-family: var(--font-tertiary);
		font-weight: 600;
		letter-spacing: -2.5;
		word-spacing: 1rem;
		text-transform: uppercase;
		padding-left: 5vw;
		-webkit-background-clip: text;
		background-clip: text;
		background-image: linear-gradient(
			to right bottom,
			var(--color-secondary-200),
			var(--color-accent-900)
		);
		color: transparent;

		@include abs.mq(md) {
			margin-bottom: 2.6rem;
		}

		@include abs.mq(sm) {
			margin-left: 0.5rem;
			margin-bottom: 1.2rem;
		}
	}
}
